<script>
import $ from "jquery";

export default {
  name: "myInfo",
  data(){
    return{
      user:{
        name:'',
        uid:'',
        ava:'',

      }
    }
  },
  methods:{},
  created(){
    fetch('/api/getUserInfo', {
      headers: { Token: localStorage.token }
    }).then(d => d.json()).then(d => {
      if (d.status == 200) {
        var un = d.data.alia === null ? d.data.username : d.data.alia
        this.user = {
          name: un,
          ava: d.data.avatar,
          uid: d.data.id
        }
        this.isLogin = d.guest == 0
        if (d.guest == 1) location.hash='/login'
      }
      else {
        localStorage.setItem('token', d.token)
        fetch('/api/getUserInfo', {
          headers: { Token: localStorage.token }
        }).then(d => d.json()).then(d => {
          if (d.status == 200)
            var un = d.data.alia === null ? d.data.username : d.data.alia
          this.user = {
            name: un,
            ava: d.data.avatar,
            uid: d.data.id
          }
          this.isLogin = d.guest == 0
          if (d.guest == 1) location.hash='/login'
        })
      }
    }).catch(e=>{
      location.hash='/login'
    })
  }
}
</script>

<template>
  <section class="w-screen flex items-center justify-center px-6 sm:px-8 lg:flex lg:w-full lg:items-center lg:justify-between lg:gap-12">
    <div class="lg:mx-auto lg:max-w-2xl">
      {{user.name}}
    </div>
  </section>
</template>

